.contact-form {
	margin: 20px auto;
	width: 100%
}

@media only screen and (min-width: 768px) {
	.contact-form {
		margin: 30px auto
	}
}

@media only screen and (min-width: 1220px) {
	.contact-form {
		margin: 40px auto
	}
}

.contact-form:first-child {
	margin-top: 0
}

.contact-form__item {
	margin-top: 15px
}

@media only screen and (min-width: 768px) {
	.contact-form__item {
		margin-top: 20px
	}
}

@media only screen and (min-width: 1220px) {
	.contact-form__item {
		margin-top: 30px
	}
}

.contact-form__item:first-child {
	margin-top: 0
}

.contact-form__item--error .contact-form__input,
.contact-form__item--error .contact-form__textarea {
	border-color: #D64541
}

.contact-form__label {
	display: block;
	font-size: 19px;
	font-weight: 600;
	color: #6C7A89
}

@media only screen and (min-width: 768px) {
	.contact-form__label {
		font-size: 19px
	}
}

@media only screen and (min-width: 1220px) {
	.contact-form__label {
		font-size: 19px
	}
}

.contact-form__input,
.contact-form__textarea {
	width: 100%;
	margin-top: 8px;
	border: 2px solid #ddd;
	border-radius: 2px;
	padding: 10px 12px;
	background: #fff;
	font-size: 19px;
	line-height: 1.4;
	color: #2A2F36;
	-webkit-transition: all 0.15s ease-in-out;
	-moz-transition: all 0.15s ease-in-out;
	-ms-transition: all 0.15s ease-in-out;
	-o-transition: all 0.15s ease-in-out;
	transition: all 0.15s ease-in-out
}

@media only screen and (min-width: 768px) {
	.contact-form__input,
	.contact-form__textarea {
		padding: 15px 18px;
		font-size: 19px
	}
}

@media only screen and (min-width: 1220px) {
	.contact-form__input,
	.contact-form__textarea {
		font-size: 19px
	}
}

.contact-form__input:focus,
.contact-form__textarea:focus {
	outline: none;
	border-color: #a020f0
}

::-webkit-input-placeholder {
