.post {
	position: relative;
	float: left;
	clear: both;
	width: 100%;
	margin-top: 30px;
	cursor: pointer
}

.post:first-child {
	margin-top: 0
}

@media only screen and (min-width: 1024px) {
	.post {
		margin-top: 60px;
		background: #f4f5f6
	}
}

.post:hover .post__image {
	-moz-transform: scale(1.008);
	-o-transform: scale(1.008);
	-ms-transform: scale(1.008);
	-webkit-transform: scale(1.008);
	transform: scale(1.008)
}

.post:hover .post__content {
	-webkit-box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
	-moz-box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
	box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1)
}

.post:hover .post__title a {
	color: #a020f0
}

@media only screen and (min-width: 1024px) {
	.post:nth-child(2n) .post__image-wrap {
		left: auto;
		right: 0
	}
	.post:nth-child(2n) .post__content-wrap {
		float: left;
		padding: 30px 0 30px 30px
	}
	.post:nth-child(2n) .post__content {
		margin: 0 -30px 0 0
	}
}

.post__image-wrap {
	position: relative;
	height: 0;
	margin: 0;
	padding-bottom: 56.25%;
	overflow: hidden
}

@media only screen and (min-width: 1024px) {
	.post__image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 50%;
		height: auto;
		padding-bottom: 0
	}
} 


.post__image {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	-webkit-transition: all 1s cubic-bezier(0.2, 0.3, 0, 1);
	-moz-transition: all 1s cubic-bezier(0.2, 0.3, 0, 1);
	-ms-transition: all 1s cubic-bezier(0.2, 0.3, 0, 1);
	-o-transition: all 1s cubic-bezier(0.2, 0.3, 0, 1);
	transition: all 1s cubic-bezier(0.2, 0.3, 0, 1)
}

.post__content-wrap {
	float: left;
	width: 100%;
	position: relative;
	padding: 0 20px 20px;
	background: #f4f5f6
}

@media only screen and (min-width: 768px) {
	.post__content-wrap {
		padding: 0 30px 30px
	}
}

@media only screen and (min-width: 1024px) {
	.post__content-wrap {
		float: right;
		padding: 30px 30px 30px 0;
		width: 50%
	}
}

.post__content {
	position: relative;
	margin-top: -20px;
	padding: 20px;
	background: #fff;
	-webkit-box-shadow: 0 6px 16px rgba(0, 0, 0, 0.05);
	-moz-box-shadow: 0 6px 16px rgba(0, 0, 0, 0.05);
	box-shadow: 0 6px 16px rgba(0, 0, 0, 0.05);
	-webkit-transition: all 0.4s cubic-bezier(0.2, 0.3, 0, 1);
	-moz-transition: all 0.4s cubic-bezier(0.2, 0.3, 0, 1);
	-ms-transition: all 0.4s cubic-bezier(0.2, 0.3, 0, 1);
	-o-transition: all 0.4s cubic-bezier(0.2, 0.3, 0, 1);
	transition: all 0.4s cubic-bezier(0.2, 0.3, 0, 1)
}

@media only screen and (min-width: 768px) {
	.post__content {
		margin-top: -30px;
		padding: 30px
	}
}

@media only screen and (min-width: 1024px) {
	.post__content {
		margin: 0 0 0 -30px;
		min-height: 400px
	}
}

.post__title a {
	font-weight: 700
}

